.slider {
  overflow-x: hidden;
  position: relative;
  &__item {
    transition: opacity $time-default;
    width: auto;
    &:hover {
      opacity: .75;
    }
  }
}
.slider-prev, .slider-next {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  margin-top: -15px;
  z-index: 3;
  border-radius: 50%;
  background: no-repeat center / 7px 14px;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
}
.slider-prev {
  background-image: url(../img/icon/arrow-left.png);
  left: 0;
}
.slider-next {
  background-image: url(../img/icon/arrow-right.png);
  right: 0;
}

