.search {
  // border-top: 2px solid #e9e9e9;
  // border-bottom: 2px solid #e9e9e9;
  // border-left: 2px solid #e9e9e9;
  border-radius: 3px;
  border: 2px solid #e9e9e9;
  &__select, &__input {
    border: 2px solid #e9e9e9;
    border-left-width: 1px;
    border-right-width: 1px;
  }
  &__form {
    display: flex;
    justify-content: space-between;
    margin-top: -2px;
    margin-bottom: -2px;
  }
  &__select {
    border: 2px solid #e9e9e9;
    border-left-width: 1px;
    border-right-width: 1px;
    position: relative;
    & > select {
      outline: none;
      cursor: pointer;
      padding: 20px;
      background-color: transparent;
      border: none;
    }
    // @supports (appearance: none) or (-moz-appearance: none) or (-webkit-appearance: none) {
    //   & > select {
    //     appearance: none;
    //     width: 100%;
    //     padding-right: 25px;
    //   }
    //   &::after {
    //     // content: '▼';
    //     content: '';
    //     position: absolute;
    //     right: 13px;
    //     top: 50%;
    //     margin-top: -3px;
    //     border-right: 5px solid transparent;
    //     border-left: 5px solid transparent;
    //     border-top: 5px solid #000000;
    //     display: inline-block;
    //   }
    // }
  }
  &__input {
    flex-grow: 1;
    border: 2px solid #e9e9e9;
    border-left-width: 1px;
    border-right-width: 1px;
    & > input {
      padding: 20px;
      background-color: transparent;
      border: none;
      max-width: none;
      width: 100%;
    }
    & > input::placeholder {
      line-height: 1.2;
      color: #bfbfbf;
    }
  }
  &__submit {
    padding: 20px 75px;
    text-transform: uppercase;
    color: $color-third-accent;
    background-color: transparent;
    font-weight: 700;
    border: 2px solid $color-third-accent;
    border-radius: 3px;
    cursor: pointer;
    transition-property: background-color, color;
    transition-duration: $time-default;
    &:hover {
      color: #ffffff;
      background-color: $color-third-accent;
    }
  }
}

.custom-select-opener {
  height: 100%;
  padding: 1.5em;
  padding-right: 30px;
  background-color: #fff;
  position: relative;
  &::after {
    // content: '▼';
    content: '';
    position: absolute;
    right: 13px;
    top: 50%;
    margin-top: -3px;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    border-top: 5px solid #000000;
    display: inline-block;
  }
}
.custom-select-panel {
  z-index: 2;
  border: 1px solid #e9e9e9;
  transition: none;
}
.custom-select-option {
  cursor: pointer;
  background-color: #ffffff;
}
.custom-select-option.is-selected {
  background-color: #e9e9e9;
}
.custom-select-option.is-selected::before {
  content: '';
  padding-right: 0;
}


