@media (max-width: 767px) {
  .search {
    margin-left: 20px;
    margin-right: 20px;
    border: none;
    &__form {
      flex-direction: column;
      margin-top: 0;
      margin-bottom: 0;
    }
    &__select, &__input, &__submit {
      margin-bottom: 10px;
      border-width: 2px;
    }
  }
}
