.footer {
  // position: relative;
  overflow-x: hidden;
  &__inner {
    padding: 20px 10px;
    width: 100%;
    background-color: #f9f9f9;
    margin: 0 auto;
  }
  &__menu {
    display: flex;
    justify-content: center;
    // margin-bottom: 22px;
    font-size: 16px;
    text-align: center;
    text-align-last: center;
    & .nav-menu__list {
      display: flex;
      width: 100%;
      justify-content: space-around;
      flex-wrap: wrap;
    }
    & .nav-menu__item {
      margin-left: 15px;
      margin-right: 15px;
    }
    & .nav-menu__link {
      white-space: nowrap;
    }
  }
  &__line {
    height: 1px;
    width: 100vw;
    background-color: transparent;
    margin: 10px 0;
    margin-left: -20px;
  }
  &__info {
    text-align: center;
    font-size: 12px;
  }
}

@media (min-width: 1200px) {

}
@media (max-width: 1199px) {
  .footer {
    background-color: #f9fafb;
    &__inner {
      background-color: transparent;
    }
  }
}
@media (max-width: 991px) {
  .footer {
    &__menu {
      // border-bottom: 1px solid #dfdfdf;
      // position: relative;
      & .nav-menu__item {
        margin-top: 10px;
      }
    }
    &__line {
      // display: block;
      background-color: #dfdfdf;
    }
  }
}
@media (max-width: 767px) {}
@media (max-width: 480px) {
  .footer {
    &__menu {
      font-size: 14px;
    }
    &__info {
      color: #929292;
    }
  }
}
@media (max-width: 320px) {

}