$fz-logo-text: 12px;
.header {
  border-bottom: 2px solid $color-third-accent;
  &__top {
    border: 1px solid #e0e0e0;
    background-color: #f5f5f5;
  }
  &__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
  }
  &__menu {}
  &__auth {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__auth-link {
    color: $color-main-accent;
    display: inline-block;
    // position: relative;
    &:hover {
      text-decoration: underline;
    }
    &::before { //TODO уточнить, возможно иконка не к этой кнопке, а в качестве отдельной кнопки
      content: '';
      display: inline-block;
      vertical-align: middle;
      margin-right: 12px;
      background: url(../img/icon/heart.png) no-repeat center / cover;
      width: 23px;
      height: 20px;
    }
  }
  &__ad-btn {
    display: block;
    color: #ffffff;
    border-radius: 3px;
    background-color: $color-third-accent;
    padding: 14px 17px;
    margin-left: 22px;
    transition-property: background-color, color;
    transition: $time-default;
    border: 1px solid $color-third-accent;
    &:hover {
      color: $color-third-accent;
      background-color: #fff;
    }
  }
  &__bottom {
    padding: 20px 0;
  }
  &__bottom-inner {

  }
  &__logo {}
}

.nav-menu {
  &__list {
    
  }
  &__item {
    display: inline-block;
    color: $color-second-accent;
    & + & {
      margin-left: 40px;
    }
    &:hover {
      text-decoration: underline;
    }
  }
  &__link {}
}

.logo {
  display: flex;
  align-items: center;
  &__link {
    margin-right: 8px;
  }
  &__img {
    // width: 100%;
    // flex-shrink: 0;

  }
  &__text {
    max-width: 100px;
    font-size: $fz-logo-text;
  }
}

.burger {
  width: 33px;
  display: none;
  padding-top: 6px;
  position: relative;
  z-index: 15;
  & > span {
    display: block;
    width: 100%;
    height: 2px;
    background-color: $color-third-accent;
    margin-bottom: 6px;
    transition-duration: $time-default;
    transform-origin: center;
  }
  &.active {
    & > span:nth-of-type(2) {
      opacity: 0;
    }
    & > span:nth-of-type(1) {
      transform: translateY(8px) rotate(45deg);
    }
    & > span:nth-of-type(3) {
      transform: translateY(-8px) rotate(-45deg);
    }
    // position: fixed;
  }
}


@media (min-width: 1200px) {

}
@media (min-width: 992px) and (max-width: 1199px) {

}
@media (max-width: 991px) {
  .burger {
    display: block;
    margin-right: 45px;
  }
  .header {
    &__top {
      padding-top: 30px;
      position: fixed;
      z-index: 10;
      right: 0;
      top: 0;
      max-width: 450px;
      width: 100%;
      height: 100%;
      background-color: #e0e0e0;
      border: none;
      transition-property: transform;
      transition-duration: $time-default;
      transform: translateX(100%);
      &.active {
        transform: translateX(0);
      }
    }
    &__top-inner {
      flex-direction: column;
      align-items: flex-start;
      padding-left: 75px;
    }
    
    &__menu {
      & .nav-menu__list {
        display: flex;
        flex-direction: column;
      }
      & .nav-menu__item {
        font-size: 20px;
        margin-left: 0;
        margin-bottom: 30px;
      }
    }
    &__auth {
      flex-direction: column;
      align-items: flex-start;
    }
    &__auth-link {
      font-size: 20px;
      margin-bottom: 20px;
    }

    &__ad-btn {
      margin-left: 0;
      font-size: 16px;
    }
    &__bottom {

    }
    &__bottom-inner {
      display: flex;
      position: relative;
    }
  }
}
