.main {
  display: flex;
  justify-content: space-between;
  &__content {
    max-width: 850px;
    width: 100%;
  }
  &__slider {
    margin-bottom: 50px;
  }
  &__sidebar {
    max-width: 270px;
    width: 100%;
  }
}

.new-ads {
  &__title {
    margin-bottom: 30px;
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
  &__item {
    &:not(:nth-of-type(4n)) {
      margin-right: 20px;
    }
    // flex-shrink: 1;
    // flex-grow: 0;
  }
  &__more {
    display: block;
    margin: 0 auto;
    border-radius: 3px;
    border: 3px solid #dedede;
    background-color: transparent;
    color: #afafaf;
    font-weight: 700;
    text-transform: uppercase;
    padding: 21px 44px;
    cursor: pointer;
    transition-property: background-color color;
    transition-duration: $time-default;
    &:hover {
      background-color: #dedede;
      color: #ffffff;
    }
  }
}



@media (min-width: 1200px) {

}
@media (max-width: 1199px) {
  .main {
    &__content {
      margin: 0 auto;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 867px) {
  .new-ads {
    &__list {
      justify-content: center;
    }
    &__item {
      &:not(:nth-of-type(4n)) {
        margin-right: 0px;
      }
      &:not(:nth-of-type(3n)) {
        margin-right: 20px;
      }
    }
  }
}
@media (max-width: 767px) {
  .main {
    &__slider {
      margin-bottom: 40px;
    }
  }
}
@media (max-width: 650px) {
  .new-ads {
    &__item {
      &:not(:nth-of-type(3n)) {
        margin-right: 0px;
      }
      &:not(:nth-of-type(2n)) {
        margin-right: 20px;
      }
    }
  }
}
