html {

}
body {
  font-size: $fz-default;
  font-family: $ff-gotham;
  // line-height: 1;
  &.lock {
    overflow-y: hidden;
  }
}
.wrapper {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  & main {
    flex-grow: 1;
  }
}
.container {
  max-width: 1160px;
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin: 0 auto;
  line-height: 36px;
  &__title {
    font-size: $fz-title;
    font-weight: 700;
  }
  &__subtitle {
    font-size: $fz-subtitle;
    font-weight: 500;
  }
}



.ruble {
  &::after {
    // content: ' ₽';
    content: '';
    display: inline-block;
    height: 0.7em;
    width: 1em;
    background: url(../img/icon/ruble.svg) no-repeat center / contain;
    // font-weight: 400;
  }
}

.section-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
}

.section-title-small {
  font-size: 18px;
  font-weight: 700;
}

.mobile-btn-ad {
  display: none;
  align-items: center;
  border-radius: 3px;
  background-color: $color-third-accent;
  position: fixed;
  z-index: 1;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 100px);
  transition-property: transform;
  transition-duration: $time-long;
  &.visible {
    transform: translate(-50%, 0);
  }
  &__link {
    display: inline-block;
    padding: 16px 20px;
    color: #ffffff;
    font-weight: 700;
    white-space: nowrap;
    &::before {
      content: '';
      display: inline-block;
      vertical-align: bottom;
      background: url(../img/icon/plus.png) no-repeat center / contain;
      width: 16px;
      height: 16px;
      margin-right: 10px;
    }
  }
}


