.card {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 197px;
  width: 100%;
  // line-height: 1;
  &--small {
    flex-direction: row;
    margin-bottom: 20px;
    max-width: none;
  }
  // &:not(:nth-of-type(4n)) {
  //   margin-right: 20px;
  // }
  // &--small:not(:nth-of-type(4n)) {
  //   margin-right: 0;
  // }
  &:hover &__title {
    color: $color-third-accent;
    text-decoration-color: $color-third-accent;
  }
  &__img {
    margin-bottom: 20px;
    // max-width: 197px;
    max-height: 175px;
    width: 100%;
    height: 100%;
  }
  &--small &__img {
    max-width: 77px;
    max-height: 77px;
    margin-bottom: 0;
    margin-right: 17px;
  }
  &__title {
    color: $color-main-accent;
    font-weight: 700;
    text-decoration: underline;
    text-decoration-color: #e3e9ed;
    margin-bottom: 8px;
    transition-property: text-decoration-color, color;
    transition-duration: $time-default;
  }
  &__geo {
    margin-bottom: 7px;
  }
  &__date {
    color: #bfbfbf;
    font-size: 12px;
    margin-bottom: 6px;
  }
  &__price {
    font-size: 18px;
    font-weight: 700;
  }
}

