.sidebar {
  &__recent {
    margin-bottom: 10px;
  }
  &__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 18px;
  }
  &__recent-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.service-item {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
  &:hover &__title {
    text-decoration-color: $color-second-accent;
  }
  &__img {
    max-width: 77px;
    width: 100%;
    margin-right: 20px;
    & > img {
      width: 100%;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title {
    color: $color-second-accent;
    font-size: 16px;
    font-weight: 700;
    text-decoration-line: underline;
    text-decoration-color: transparent;
    transition: text-decoration-color $time-default;
    margin-bottom: 10px;
  }
  &__subtitle {}
}


